import { Application } from './index'

export default (app: Application) => {
  window.addEventListener('message', (event: MessageEvent) => {
    const { action, data } = event.data
    switch (action) {
      case 'to':
        app.transform.to(data.module, data.query, data.history)
        break
      case 'back':
        history.back()
        break
      case 'forward':
        history.forward()
        break
    }
  })
  return
}
