import { Application } from '../../Application'
import { TransformHistory } from '../history'
import { TransformAnimateEvent, AnimationConfig } from '../../types'

class TransformAnimation extends TransformHistory {
  constructor (app: Application) {
    super(app)
  }

  public checkAnimationNamed (): boolean {
    return !!this.getAnimationNames()
  }

  public getAnimationNames (): string | boolean | undefined | AnimationConfig {
    if (!this.modulu || this.modulu.rel !== 'module') return false
    if (this.isOverscrollHistoryNavigatio) return false
    const animationNames = this[this.history !== 1 && this.historyDirection === -1 ? 'modulu' : 'module'].config.animation
    if (animationNames === true || animationNames === 'inherit') {
      return this.options.defaultAnimation
    }
    return animationNames
  }

  public async getAnimationGroup (): Promise<[(e: TransformAnimateEvent) => void | Promise<boolean>, (e: TransformAnimateEvent) => void | Promise<boolean>] | boolean> {
    let animationFunction: [(e: TransformAnimateEvent) => void, (e: TransformAnimateEvent) => void] | undefined
    const animationNames = this.getAnimationNames()
    if (typeof animationNames === 'string') {
      animationFunction = await this.getAnimationByName(animationNames)
    } else if (typeof animationNames?.[0] === 'function') {
      return animationNames as [(e: TransformAnimateEvent) => void | Promise<boolean>, (e: TransformAnimateEvent) => void | Promise<boolean>]
    }

    if (typeof animationFunction?.[0] !== 'function') {
      return false
    }

    return animationFunction
  }

  public async getAnimationOneSide (backset: number): Promise<((e: TransformAnimateEvent) => void | Promise<boolean>) | undefined> {
    const animationGroup = await this.getAnimationGroup()
    if (backset >= 0) {
      switch (typeof animationGroup) {
        case 'function':
          return animationGroup
        case 'object':
          return animationGroup[animationGroup.length === 2 ? backset : 0]
        default:
          return
      }
    }
    return
  }

  public async getAnimationByName (type: string): Promise<[(e: TransformAnimateEvent) => void | Promise<boolean>, (e: TransformAnimateEvent) => void | Promise<boolean>] | undefined> {
    switch (type) {
      case 'flip':
        return new Promise((resolve) => {
          import('./preset/flip').then((module) => {
            const flip = module.default
            resolve([flip(4), flip(4)])
          })
        })
      case 'flipLeft':
      case 'flip-left':
        return new Promise((resolve) => {
          import('./preset/flip').then((module) => {
            const flip = module.default
            resolve([flip(3), flip(3)])
          })
        })
      case 'flipDown':
      case 'flip-down':
        return new Promise((resolve) => {
          import('./preset/flip').then((module) => {
            const flip = module.default
            resolve([flip(2), flip(2)])
          })
        })
      case 'flipRight':
      case 'flip-right':
        return new Promise((resolve) => {
          import('./preset/flip').then((module) => {
            const flip = module.default
            resolve([flip(2), flip(2)])
          })
        })
      case 'flipUp':
      case 'flip-up':
        return new Promise((resolve) => {
          import('./preset/flip').then((module) => {
            const flip = module.default
            resolve([flip(0), flip(0)])
          })
        })
      case 'fade':
        return new Promise((resolve) => {
          import('./preset/fade').then((module) => {
            const fade = module.default
            resolve([fade(1), fade(0)])
          })
        })
      case 'zoom':
        return new Promise((resolve) => {
          import('./preset/zoom').then((module) => {
            const zoom = module.default
            resolve([zoom(1), zoom(0)])
          })
        })
      case 'slide':
      case 'slideLeft':
      case 'slideleft':
      case 'slide-left':
        return new Promise((resolve) => {
          import('./preset/slide').then((module) => {
            const slide = module.default
            resolve([slide(1), slide(3)])
          })
        })
      case 'slideRight':
      case 'slideright':
      case 'slide-right':
        return new Promise((resolve) => {
          import('./preset/slide').then((module) => {
            const slide = module.default
            resolve([slide(3), slide(1)])
          })
        })
      case 'slideUp':
      case 'slideup':
      case 'slide-up':
        return new Promise((resolve) => {
          import('./preset/slide').then((module) => {
            const slide = module.default
            resolve([slide(2), slide(0)])
          })
        })
      case 'slideDown':
      case 'slidedown':
      case 'slide-down':
        return new Promise((resolve) => {
          import('./preset/slide').then((module) => {
            const slide = module.default
            resolve([slide(0), slide(2)])
          })
        })
      default:
        return Promise.resolve(undefined)
    }
  }
}

export {
  TransformAnimation
}
